*{
    margin:0;
    padding: 0;
    box-sizing: border-box;
}

.app{

  height: 100%;

  .sections{
      width: 100%;
      height: calc(100vh - 70px);
      background-color: lightsalmon;
      position: relative;
      top: 70px;
      scroll-behavior: smooth;
      scroll-snap-type: y mandatory;
      scrollbar-width: none; //for firefox
      &::-webkit-scrollbar{
          display: none;
      }

      > *{
          width: 100%;
          height: calc(100vh - 70px);
          scroll-snap-align: start;
      }

  } 
}

